import { tooltip } from "../Bindings/Chakra.fs.js";

export function Tooltip(tooltipInputProps) {
    const children = tooltipInputProps.children;
    const input = tooltipInputProps.input;
    const arg00 = {
        backgroundColor: "gray.77",
        color: "black",
        hasArrow: input.hasArrow,
        label: input.label,
        placement: input.placement,
        zIndex: 20000,
    };
    return tooltip()(arg00)(children);
}

